@import url('https://google-fonts.mirrors.sjtug.sjtu.edu.cn/css?family=Sacramento:400');
@import url('https://google-fonts.mirrors.sjtug.sjtu.edu.cn/css?family=Ma+Shan+Zheng&display=swap');

.App {
  text-align: center;
}


.App-header {
 background-color: #f6f8fa ;
  /* background-color: #262626; */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-evenly; */
  gap:50px;
  font-size: calc(10px + 10vmin);
  color: black;
}

.firstflexout {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 100%;
}

.react-activity-calendar {
  width: 100vh;
}

.thetile {
  font-size: 1.5rem;
  margin: 1em;
}

.styles-module_container__GBNxC {
  max-width: 95% !important;
}

.styles-module_calendar__sT1ND {
  height: 140px;
}

.neonText {
  animation: flicker 1.5s infinite alternate;
  
}

h1[class="neonText"] {
  font-family: "Ma Shan Zheng", cursive;
  font-size: 2.5rem;
  font-weight: 400;
  letter-spacing: 0.5rem;
  margin: 0;
  color:rgb(243, 219, 46);
  
}

h1 {
  font-size: 2rem;
  margin: 0;   
}
/* Flickering animation */
@keyframes flicker {
    
  0%, 18%, 22%, 25%, 53%, 57%, 100% {

      text-shadow:
      0 0 .2rem rgb(255, 255, 255),
      0 0 .1rem rgb(255, 255, 255),
      0 0 3rem rgb(243, 219, 46),
      0 0 4rem rgb(243, 219, 46),
      0 0 0.2rem rgb(243, 219, 46),
      0 0 0.2rem rgb(243, 219, 46),
      0 0 0.8rem rgb(243, 219, 46);
  
  }
  
  20%, 24%, 55% {        
      text-shadow: none;
  }    
}


/* Additional styling */
  
h2 {
 font-size: 1.8rem;
 font-family: "Sacramento", sans-serif;
 margin: 0;
}
.container {
  margin-top: 20vh;
}



h1, h2 {
  text-align: center;
  font-weight: 300;

}

.zhaopai {
  background-image:url("./wsj.jpg") ;
  background-position: top left;
  background-repeat: no-repeat;
  background-size: cover;
  /* animation: flicker 1.5s infinite alternate;   */
  border: 0.2rem solid rgb(243, 219, 46);
  border-radius: 2rem;
  padding: 0.4em;
  margin: 0.5em;
  margin-top:0.8em;
  /* position: absolute;
  top: 8%; */
  box-shadow: 0 0 .2rem rgb(255, 255, 255),
            0 0 .2rem rgb(255, 255, 255),
            0 0 2rem rgb(243, 219, 46),
            0 0 0.8rem rgb(243, 219, 46),
            0 0 2.8rem rgb(243, 219, 46),
            inset 0 0 1.3rem rgb(243, 219, 46); 
}

@keyframes pulsate {
    
  100% {

      text-shadow:
      0 0 4px rgb(255, 255, 255),
      0 0 11px rgb(255, 255, 255),
      0 0 19px rgb(255, 255, 255),
      0 0 40px rgb(83, 7, 113),
      0 0 80px rgb(83, 7, 113),
      0 0 90px rgb(83, 7, 113),
      0 0 100px rgb(83, 7, 113),
      0 0 150px rgb(83, 7, 113);
  
  }
  
  0% {

    text-shadow:
    0 0 4px rgb(255, 255, 255),
    0 0 10px rgb(255, 255, 255),
    0 0 18px rgb(255, 255, 255),
    0 0 38px rgb(83, 7, 113),
    0 0 73px rgb(83, 7, 113),
    0 0 80px rgb(83, 7, 113),
    0 0 94px rgb(83, 7, 113),
    0 0 140px rgb(83, 7, 113);

}
}


#things {
  font-size: 1rem;
  
}

/* .dothings {
  position: absolute;
  bottom: 20%;
} */

@media screen and (max-width: 950px) and (max-height: 500px) {
  h2[class="neonText"] {
    display: none;
  }
  .zhaopai {
    
    margin: 0.4em;
  }
  .second2{
    display: none;
  }

  .thetile {
    display: none;
  }

  .App-header {
     gap: 10px;

   }
  

  .styles-module_container__GBNxC {

    max-width: 95% !important;
  }

  .dothings {
    display: none;
  }
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align:left
}

ul li::before{
  content: '\27B5';
  margin: 0 0.5em;
  color: rgb(161, 102, 47);
  line-height: 1.5;
}